@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .second_section {
        .reasons_block {
            align-items: flex-start;
            .text {
                width: 312px; } } }
    .footer {
        .footer_driver {
            .link {
                img {
                    width: 96px;
                    height: 34px; } } } }
    .xs-visible {
        display: none; } }
@media (max-width: 992px) {
    .menu {
        &_item {
            padding-right: 7px; }
        &_link {
            font-size: 10px; } }
    .subheader_official {
        font-size: 12px; }
    .subheader_phone {
        font-size: 18px; }
    .second_section {
        height: 1283px;
        .reasons_block {
            .text {
                width: 245px; } }
        .icon {
            width: 112px;
            height: 97px; } }
    .mobile_item {
        width: 231px;
        height: 264px; }
    .footer {
        .footer_driver {
            .link {
                flex-direction: column;
                align-items: center; }
            .icons {
                flex-direction: column;
                align-items: center; } } }
    .world {
        .seeall {
            left: 38%; } }
    .xs-visible {
        display: none; } }
@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8 );
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none; }
        &_link {
            font-size: 13px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .choice_img {
        width: 100%; }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader_phone {
        font-size: 19px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-top: 21px; }
    .subheader_btn {
        width: 165px;
        height: 38px;
        margin: 10px 41px; }
    .second_section {
        height: 1514px;
        .reasons_block {
            .text {
                width: 356px; } }
        .icon {
            width: 99px;
            height: 89px; } }
    footer {
        .footer_driver {
            .description_title {
                a {
                    font-size: 12px;
                    line-height: none; } }
            .description_subtitle {
                a {
                    font-size: 9px;
                    line-height: 20px; } } } }
    .world {
        .seeall {
            position: static;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            margin-top: 80%; } } }

@media ( max-width: 575px  ) {
    .subheader_official {
        display: none; }
    .subheader_logo {
        margin-top: 27px;
        img {
            width: 130px;
            height: 15px; } }
    .subheader_phone {
        font-size: 14px;
        margin-top: 25px; }
    .subheader_btn {
        width: 107px;
        height: 30px;
        margin: 8px 13px;
        font-size: 10px; }
    .promo {
        min-height: 400px;
        padding: 112px 0 35px 0;
        &_header {
            font-size: 30px; }
        &_subheader {
            margin-top: 30px;
            font-size: 20px; }
        .promo-desc {
            display: none; }
        &_btn {
            width: 250px;
            height: 56px;
            margin: 72px auto 0 auto; } }
    .menu {
        width: 80%; }
    .label_title {
        font-size: 16px;
        line-height: 16px;
        margin-top: 15px; }
    .second_section {
        height: 2660px;
        .reasons_block {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 29px;
            flex-direction: column;
            .text {
                width: 100%;
                text-align: left;
                .title {
                    margin-bottom: 10px; } }
            .icons {
                width: 72px;
                height: 59px; } }
        .icon {
            width: 100px;
            height: 100px;
            margin: 10px 0 20px 0; } }
    .label_subtitle {
        margin-top: 10px; }
    .mobile_item {
        width: 100%; }
    .choice {
        min-height: 550px;
        background: none;
        padding: 57px 0 0px 0; }
    .require {
        padding: 0px 0 84px 0; }
    .world {
        height: 540px; }
    .footer {
        .lien {
            width: 90%; }
        .footer_driver {
            display: flex;
            flex-direction: column;
            .icons {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                margin: 0 auto; }
            .description {
                width: 100%;
                &_subtitle {
                    padding: 0 10px; } }
            .link {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                padding: 10px 15px;
                margin: 15px 26px; } } } }


