header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.hamburger {
	display: none; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		text-decoration: none;
		font-weight: 300;
		font-size: 15px;
		color: #FFFFFF; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }


.subheader {
	position: absolute;
	top: 70%;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		font-weight: 400;
		font-size: 13px;
		margin-top: 6px;
		color: white;
		opacity: 0.5; }
	&_call {
		font-weight: 300;
		font-size: 12px;
		color: white;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		font-weight: 400;
		font-size: 22px;
		text-decoration: none;
		text-align: center;
		text-transform: uppercase;
		color: white;
		line-height: 12px; }
	&_btn {
		width: 172px;
		height: 46px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		margin-top: 21px;
		border-radius: 4px;
		text-align: center;
		border: none;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		@include btn_hover; } }
.promo {
	min-height: 800px;
	background: url(../img/uber_bg.png) center center/cover no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 60px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-align: center;
		margin: 0; }
	&_subheader {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 36px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-align: center;
		margin: 0; }
	&-desc {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		color: #FFFFFF;
		margin-top: 23px; }
	&_btn {
		display: block;
		width: 259px;
		height: 63px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		margin: 118px auto 0 auto;
		border: none;
		@include btn_hover; } }

.second_section {
	height: 900px;
	padding: 18px 0 64px 0;

	.icon {
		display: flex;
		background: #1EACC7;
		border-radius: 100%;
		width: 113px;
		height: 113px;
		align-items: center;
		justify-content: center;
		margin-right: 26px; }
	.icons {
        display: flex;
        width: 80px;
        height: 66px;
        z-index: 2; }
	.title {
		display: flex;
		font-style: normal;
		font-weight: 400;
		font-size: 17px;
		line-height: 21px;
		color: #1EACC7; }

	.subtitle {
		display: flex;
		font-style: normal;
		font-weight: 300;
		font-size: 15px;
		line-height: 21px;
		color: #202020; }

	.reasons_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px;
		.text {
			width: 343px; } } }


.label {
	position: relative;
	font-style: normal;
	font-weight: 400;
	font-size: 11px;
	padding: 7px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	width: 115px;
	height: 27px;
	text-align: center;
	background: #000000;
	margin: 0 auto;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; } }
.label_title {
	position: relative;
	font-style: normal;
	font-weight: 700;
	font-size: 38px;
	line-height: 48px;
	text-align: center;
	text-transform: uppercase;
	color: #222222;
	margin-top: 33px;
	z-index: 2;
	&_white {
		color: #fff; } }
.label_subtitle {
	position: relative;
	font-style: normal;
	font-weight: 400;
	font-size: 17px;
	line-height: 21px;
	text-align: center;
	color: #222222;
	z-index: 2;
	&_white {
		color: white; } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }

	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../icons/mobile icons/Карточка 1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../icons/mobile icons/Карточка 2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../icons/mobile icons/Карточка 3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../icons/mobile icons/Карточка 4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../icons/mobile icons/Карточка 5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../icons/mobile icons/Карточка 6.png') center (center / cover) no-repeat; }
		&_subtitle {
			font-weight: 400;
			font-size: 24px;
			line-height: 31px;
			text-align: center;
			text-transform: uppercase;
			color: #FFFFFF; }
		&_plus {
			display: flex;
			justify-content: center;
			text-align: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background: #000000;
			border-radius: 100%;
			margin: 0 auto;
			padding: 6px;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				width: 14px;
				height: 2px;
				background-color: $main-color;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				display: block;
				transform: translateX(-50%);
				left: 50%;
				height: 15px;
				width: 2px;
				background-color: $main-color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $second-color; } } } } }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }
.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		font-weight: 400;
		font-size: 22px;
		line-height: 24px;
		color: #1CA8C3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-weight: 300;
			font-size: 17px;
			line-height: 24px;
			margin-bottom: 10px;
			&::before {
				position: absolute;
				content: '';
				display: block;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue.png') center (center / cover) no-repeat; } } }
	&_desc {
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		color: #1CA8C3; }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&::before {
					background: url('../icons/check_red.png') center (center / cover) no-repeat; } } } } }

.world {
	position: relative;
	width: 100%;
	height: 720px;
	padding: 48px 0 21px 0;
	background: url('../img/world_bg.png') center (center/cover) no-repeat;
	.seeall {
		position: absolute;
		bottom: 10%;
		left: 45%;
		font-weight: 400;
		font-size: 15px;
		line-height: 68px;
		text-align: center;
		text-transform: uppercase;
		color: #1FBAD6; } }

footer {
	padding: 25px 0 20px 0;
	.lien {
		width: 301px;
		height: 7px;
		background: #1FBAD6;
		margin: 0 auto; }
	.footer_driver {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px;
		.icons {
			display: flex;

			img {
				width: 30px;
				height: 30px; } }
		.description {
			width: 390px;
			margin: 0 auto;
			&_title {
				display: flex;
				justify-content: space-between;
				padding: 0 10px;
				a {
					font-weight: 300;
					font-size: 15px;
					line-height: 68px;
					text-transform: uppercase;
					color: #070716; } }
			&_subtitle {
				display: flex;
				justify-content: space-between;
				margin-top: 10px;
				a {
					font-weight: 300;
					font-size: 11px;
					line-height: 68px;
					text-transform: uppercase;
					color: #999999; } }
			.lang {
				display: block;
				margin: 0 auto;
				color: #1fbad6;
				font-size: 11px;
				font-weight: 400;
				margin-top: 19px;
				text-transform: uppercase;
				text-align: center; } }
		.link {
			img {
				width: 140px;
				height: 45px; } } }
	.footer_driver > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } } }
.xs-visible {
        display: none; }
